  }else{
//  this.chaxun(this.currentPage,this.pagesize);
    this.seeker(this.stt,this.edt,this.currentPage,this.pagesize);
  }
},
//
handlePrevChange:function(){
  if(this.indx == 0){
//  this.fund();
    this.loading(this.currentPage,this.pagesize);
  }else{
//  this.chaxun(this.currentPage,this.pagesize);
    this.seeker(this.stt,this.edt,this.currentPage,this.pagesize);
  }
},
//
handleNextChange:function(){
  if(this.indx == 0){
//  this.fund();
    this.loading(this.currentPage,this.pagesize);
  }else{
//  this.chaxun(this.currentPage,this.pagesize);
    this.seeker(this.stt,this.edt,this.currentPage,this.pagesize);
