          var res = [];
          for(var i=0; i<arr.length; i++){
            if(res.indexOf(arr[i]) == -1){
             res.push(arr[i]);
            }
          }
          return res;
        }
        let arr = changedouhao(this.form.antistop).split(',');
        let ArrAy = unique(arr).join(',');
        console.log(ArrAy);
        if(this.form.enhance ==1 || this.form.cleaning == 1 || this.form.sr == 1 || this.form.ks == 1 || this.form.li == 1 || this.form.gi == 1 || this.form.idx == 1){
          if(this.form.ks == 1){
            if(this.form.typeId != '' || ArrAy !=''){
              if(this.form.idx == 1 && this.form.pnum == ''){
                this.$message.error("");
              }else{this.feng(ArrAy);}
            }else{this.$message.error('')}
          }else{
            this.form.typeId = 0;
            if(this.form.idx == 1 && this.form.pnum == ''){
              this.$message.error("");//$t('logo.title')
            }else{this.feng(ArrAy);}
          }
        }else{
          this.$message.error("");
        }
      },
keyup(val){
  function changedouhao(str){
  str=str.replace(//ig,',');
  return str;
}
function unique(arr){
  var res = [];
  for(var i=0; i<arr.length; i++){
    if(res.indexOf(arr[i]) == -1){
     res.push(arr[i]);
    }
  }
  return res;
}
let arr = changedouhao(this.form.antistop).split(',');
let ArrAy = unique(arr).join(',');
console.log(ArrAy);

  console.log(ArrAy);
  var str=ArrAy;
  if (str.length>0){
    for (var i = str.length-1; i >= 0; i--){
      let unicode=str.charCodeAt(i);
      if (unicode>65280 && unicode<65375){
        this.$message.error("");
        this.form.antistop = '';
        this.formaaa.pla = ""
      }
    }
  }
},
//    
feng(ArrAy){
newtaskadd(this.form.name,this.form.tasknum,this.form.enhance,this.form.cleaning,this.form.sr,this.form.ks,this.form.li,this.form.gi,this.form.typeId,ArrAy,this.form.pnum,this.form.idx).then((res)=>{
if(res.data.ret == 200){
  this.taskid = res.data.data.taskid;
  console.log(this.taskid);
  this.$message.success("");
  this.show_task_childen=false;
  this.show_task_add=true;
}if(res.data.ret == 405){
  this.$message.error(res.data.msg);
}
}).catch((error)=>{

    console.log(error);
  });
},
      dianjia(){
        this.dianji1 = false;
      },
//    
      handleClick(row) {
        console.log(row);
      },
//    
      newTask(){
        this.show_task = true;
        this.show_task_childen = true;
        var myDate = new Date();
        var y = myDate.getFullYear().toString(),//
        m = ("0" + (myDate.getMonth() + 1)).slice(-2).toString(),//
        d = myDate.getDate().toString(),//
        h = myDate.getHours().toString(),//
        fen = myDate.getMinutes().toString(),//
        s = myDate.getMilliseconds().toString();//
        var mydate = y + m + d + h + fen + s;
        var mydates = y + "" + m + "" + d + "" + h + "" + fen + "";
        console.log(mydates);
        this.form.tasknum = mydate;
        this.form.name = mydate;
      },
//    
      guan(){
        this.form.antistop = '';
        this.form.typeId = "0";
        this.targetantistop = !this.targetantistop;
      },
      idex(){
        this.form.pnum = '';
        this.voice = !this.voice;
      },

      show(){
        this.targetantistop = false;
        this.voice = false;
        this.form.ks = 0;
        this.form.idx = 0;
      },
//    
      conceal(){
        this.show_task = false;
        this.initall();
      },
//    
      submitUpload() {
        this.$refs.upload.submit();
      },
      handleRemove(file, fileList) {
//      console.log(file, fileList);
      },
      handlePreview(file) {
//      console.log(file);
      },
//    
      handleSuccess(response, file, fileList){
        this.i+=1;
//      console.log(this.i);
        this.taskid = response.data.taskid;
        this.taskfile_response = response.ret;
        if(fileList.length == this.i){
//        
          taskStart(this.taskid).then((res)=>{
//          console.log(res.data);
            this.$message.success("");
            this.show_task=false;
            this.show_task_add=false;
            this.dianji1 = true;
            this.i = 0;
            fileList.splice(0,fileList.length);
          })
          tasklist().then((res)=>{
  //        console.log(res.data);
            this.tableData = res.data.data;
            if(res.data.data==''){
              this.kong=true;
            }else{
              this.kong=false;
            }
          })
          this.initall();
        }

      },
//    
initall(){
  this.targetantistop = false;
  this.voice = false;
  this.form.enhance = 0;this.form.cleaning = 0;this.form.idx = 0;
  this.form.sr = 0;this.form.ks = 0;this.form.li = 0;this.form.typeId = "0";
  this.form.name = '';this.form.tasknum = '';this.form.gi = 0;this.form.antistop = ''
},
//    
      handleError(err, file, fileList){
//      console.log(this.type);
        this.i = 0;
      },
      addfile(file, fileList){
        console.log(file, fileList);
      },
      change(){
        console.log(this.form.typeId);
        for(var i=0; i<this.targetname.length; i++){
          if(this.targetname[i].id==this.form.typeId){
            this.form.typeName = this.targetname[i].name;
          }
        }
        console.log(this.form.typeName);
      },
    },
    data() {
      return {
        stt:'',
        edt:'',
        indx:0,
        currentPage:1,
        totalCount: 0,
        pagesize: 10,
        fenye:false,
        formaaa:{
          typeId:'',
          pla:'","'
        },
        targetantistop:false,
        voice:false,
        addfiles:[],
        inde:"",
        i:0,
        dianji1:true,
        xiaoshi1:true,
        xiaoshi:false,
        kong:false,
        taskfile_response:"",
        tasklists:[],
        show_task:false,
        show_task_childen:false,
        show_task_add:false,
        suc:false,
        taskid:"",
        tasksearch: {
          taskname:'',
          tasknum:'',
          tasktype:'',
          taskstatus:'',
          handler:'',
          starttime:'',
          endtime:''
        },
        fileList: [],
        tableData: [],
        targetname: [],
        form: {
          typeId:'',
          typeName:'',
          name: '',
          tasknum: '',
          antistop: '',
          pnum:'',
          enhance:0,
          cleaning:0,
          sr:0,
          ks:0,
          li:0,
          gi:0,
          idx:0
        },
        http:"http://192.168.1.118/task/upload"
      }
    }

  }
</script>



